.location {
    height: 100vh;
    width: 100%;
    background-color: #6983ca;
    overflow: hidden;
    border-top-left-radius: 30px 20px;
    border-top-right-radius: 30px 20px;

    .wrapper {
        display: flex;
        width: 100%;
        height: 100%;
        align-items: center;
        justify-content: center;


        .left {
            height: 50%;
            display: flex;
            flex-direction: column;
            padding: 10px;
            flex: 1;
            gap: 30px;
            position: relative;
            margin-top: 800px;
            margin-left: 40px;

            h1 {
                margin: -31px;
            }

            p {
                margin-left: -24px;
                margin-top: 20px;
                text-align: justify;
            }


            button {
                background-color: #7f95dc;
                border: 1px solid lightgray;
                border-radius: 5px;
                cursor: pointer;
                color: lightgray;
                padding: 20px;
                width: 50%;
                position: absolute;
                left: 20%;
                bottom: 100px;


                &:hover {
                    background-color: #849be7;
                    border-color: lightgray;
                }
            }
        }

        .right {
            flex: 2;
            padding: 20px;
            margin-top: 500px;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 20px;
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .location {
        .wrapper {
            display: flex;
            flex-direction: column;


            .left {
                margin-top: 0;
                height: 10%;

                h1 {
                    margin-top: 519px;
                }
            }

            .right {
                flex: 0.5;

                img {
                    height: 120%;
                    width: 100%;
                }
            }
        }
    }
}